import React, { Component } from "react";

class DictSelect extends Component {
  constructor(props) {
    super(props);
    this.state = { value: 1, nameDic: "100 Words" };

    this.handleChange = this.handleChange.bind(this);
  }

  // собственный метод которому назначено быть обрабочиком вызывает метод полученный из родительской компоненты
  handleChange(event) {
    // this.setState({value: event.target.value})
    this.props.onDictSelectChange(event.target.value);
  }

  render() {
    // console.log(Array.isArray(this.props.dicts));
    return (
      <form>
        <label>
          Select Dictionary
          <select
            className="browser-default custom-select"
            value={this.props.currentDict}
            onChange={this.handleChange}
          >
            {this.props.dicts.map((dic, i) => (
              <option key={i} value={i}>
                {dic.name}
              </option>
            ))}
            {/* <option value="0">100 Words</option>
            <option value="1">500 Words</option>
            <option value="2">1000 Words</option>
            <option value="3">3000 Words</option>
            <option value="4">Quotes</option> */}
          </select>
        </label>
      </form>
    );
  }
}

export default DictSelect;
