import React, { Component } from "react";
// import ReactDOM from "react-dom";
import "@fortawesome/fontawesome-free/css/all.min.css";
import "bootstrap-css-only/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import "./index.css";
import {
  MDBBtn,
  MDBInput,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBModalFooter,
  MDBIcon,
  MDBBadge,
  MDBContainer,
  MDBRow,
  MDBCol
} from "mdbreact";

import TypeFast from "./components/typeFast";
import DictSelect from "./components/dictSelect";
import Dictionary from "./components/dictionaries";

const APPINI = {
  name: "Type Fast",
  ver: "0.0.1",
  copyright: "Vebkomas"
};

class GameResult extends Component {
  render() {
    function formatms2time(a) {
      const ms = Math.floor(a / 100) % 10;
      const sec = Math.floor(a / 1000) % 60;
      const min = Math.floor(a / 60000) % 60;
      return (
        min.toString().padStart(2, "0") +
        ":" +
        sec.toString().padStart(2, "0") +
        "." +
        ms
      );
    }

    return (
      <React.Fragment>
        <div className="media mt-1">
          <h3 className="h3-responsive font-weight-bold mr-3">
            {this.props.time}
          </h3>
          <div className="media-body mb-3 mb-lg-3">
            <MDBBadge
              color="danger"
              className="ml-2 float-right"
              onClick={() => this.props.onDelete(this.props.id)}
            >
              -
            </MDBBadge>
            <h6 className="mt-0 font-weight-bold">{this.props.dict} </h6>{" "}
            <hr className="hr-bold my-2" />
            {this.props.score && (
              <React.Fragment>
                {this.props.score.map(rec => (
                  <p className="font-smaller mb-0" key={rec.id}>
                    <MDBIcon icon="location-arrow" /> {formatms2time(rec.time)}{" "}
                    {rec.speed}CPM
                  </p>
                ))}
                {/* <p className="font-smaller mb-0">
                  <MDBIcon icon="location-arrow" /> {this.props.score.time}{" "}
                  {this.props.score.speed}CPM
                </p> */}
              </React.Fragment>
            )}
          </div>
        </div>
        {this.props.description && (
          <p className="p-2 mb-4  blue-grey lighten-5 blue-grey lighten-5">
            {this.props.description}
          </p>
        )}
      </React.Fragment>
    );
  }
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dictionaries: Dictionary,
      currentDict: 0,
      currentScores: [],
      modal: false,
      results: [
        // {
        //   id: Date.now(),
        //   dict: "100 Words",
        //   score: [
        //     { id: 1, time: 103450, speed: 314 },
        //     { id: 2, time: 200555, speed: 114 }
        //   ],
        //   date: new Date()
        //     .toTimeString()
        //     .replace(/.*(\d{2}:\d{2}:\d{2}).*/, "$1")
        // },
        // {
        //   id: Date.now() + 1,
        //   dict: "500 Words",
        //   score: [{ id: 1, time: 380, speed: 404 }],
        //   date: "21:42:00",
        //   description: "Not too bad..."
        // },
        // {
        //   id: Date.now() + 2,
        //   dict: "1000 Words",
        //   score: [{ id: 1, time: 400, speed: 291 }],
        //   date: "22:13:42"
        // }
      ]
    };
  }

  toggleModal = () => {
    this.setState({
      modal: !this.state.modal
    });
  };

  handleDelete = resultId => {
    // получить в переменной отфильтрованный массив
    const results = this.state.results.filter(res => res.id !== resultId);
    this.setState({ results });
  };

  // В этом месте у меня наметился некоторый пиздец потому что даже синтаксиса не понимаю
  // можно видеть сразу две бороды, последовательные две стрелочные конструкции и имя в квадратных скобках
  // результат тоже загадочный, в консоль вываливает больше одной строки за один раз
  handleInputChange = inputName => value => {
    const nextValue = value;
    this.setState({
      [inputName]: nextValue
    });
    // console.log(this.state);
  };

  addResult = () => {
    // console.log(this.state.currentDict);
    // console.log(this.state.dictionaries);
    var newArr = [...this.state.results];
    newArr.push({
      id: newArr.length ? newArr[newArr.length - 1].id + 1 : 1,
      dict: this.state.dictionaries[this.state.currentDict].name,
      date: new Date().toTimeString().replace(/.*(\d{2}:\d{2}:\d{2}).*/, "$1"),
      // здесь надо иметь доступ к состоянию дочерней компоненты
      score: this.state.currentScores,
      description: this.state.description
    });
    this.setState({ results: newArr, currentScores: [] });
  };

  // Обработчик для выбора значения текущего словаря
  handleDictSelectChange = dictId => {
    this.setState({
      currentDict: dictId
    });
  };

  // Обработчик для сохранения результатов текущей игры
  handleResultsChange = scores => {
    // console.log(scores);
    // console.log(this.state.currentScores);
    this.setState({
      currentScores: this.state.currentScores.concat(scores)
    });
  };

  render() {
    // console.log(Dictionary);
    return (
      <React.Fragment>
        <MDBContainer>
          <MDBRow>
            <MDBCol md="3">
              <h3 className="text-uppercase my-3">Type Fast</h3>
              <h6 className="my-3">
                Copyright: {APPINI.copyright}, 2020, ver {APPINI.ver}.
              </h6>
              <p>
                You have <b>{this.state.results.length} attempts </b> today.
              </p>
              <DictSelect
                dicts={this.state.dictionaries}
                onDictSelectChange={this.handleDictSelectChange}
              />
              {/* <h1 className="my-3">
                <MDBRow>
                  <MDBCol xs="3" className="text-center">
                    <MDBIcon icon="sun" fixed />
                  </MDBCol>
                  <MDBCol xs="9">Sunny</MDBCol>
                </MDBRow>
                <MDBRow>
                  <MDBCol xs="3" className="text-center">
                    <MDBIcon icon="thermometer-three-quarters" fixed />
                  </MDBCol>
                  <MDBCol xs="9">23°C</MDBCol>
                </MDBRow>
              </h1> */}
            </MDBCol>
            <MDBCol md="9" className="mb-r">
              <h2 className="text-uppercase my-3">Today:</h2>
              <div id="schedule-items">
                {this.state.results.map(res => (
                  <GameResult
                    key={res.id}
                    id={res.id}
                    dict={res.dict}
                    time={res.date}
                    description={res.description}
                    score={res.score}
                    // передать в дочерний компонент собственный метод
                    onDelete={this.handleDelete}
                  />
                ))}
              </div>
              <MDBRow className="mb-4">
                <MDBCol xl="3" md="6" className="mx-auto text-center">
                  <MDBBtn color="info" rounded onClick={this.toggleModal}>
                    New Game
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </MDBContainer>

        {/* Конструирование модального окна для игрового поля: */}
        <MDBModal isOpen={this.state.modal} toggle={this.toggleModal} size="lg">
          <MDBModalHeader
            className="text-center"
            titleClass="w-100 font-weight-bold"
            toggle={this.toggleModal}
          >
            {this.state.dictionaries[this.state.currentDict].name}
          </MDBModalHeader>
          <MDBModalBody>
            <TypeFast
              ini={APPINI}
              game={this.state.dictionaries[this.state.currentDict]}
              // метод для сохранения результатов игры из компоненты с игровым полем
              onResultsChange={this.handleResultsChange}
            />
            <form className="mx=3 grey-text">
              <MDBInput
                name="description"
                label="Description (optional)"
                icon="sticky-note"
                group
                type="textarea"
                getValue={this.handleInputChange("description")}
              />
            </form>
          </MDBModalBody>
          <MDBModalFooter className="justify-content-center">
            <MDBBtn
              color="info"
              onClick={() => {
                this.toggleModal();
                this.addResult();
              }}
            >
              Add result
            </MDBBtn>
          </MDBModalFooter>
        </MDBModal>
      </React.Fragment>
    );
  }
}
// ReactDOM.render(<App />, document.getElementById("root"));
export default App;
