class Input extends Component {
  constructor(props) {
    super(props);
    // это какой-то жуткий пипец, которого я понять не могу
    this.handleEnterTextChange = this.handleEnterTextChange.bind(this);

    // create a new referecne object four input control
    this.textInput = React.createRef();
  }

  // установить фокус ввода
  // componentDidMount() {
  //   try {
  //     this.textInput.current.focus()
  //   } catch(err) {
  //     console.log(err)
  //   }
  // }

  // установить фокус ввода
  componentDidUpdate() {
    try {
      this.textInput.current.focus();
    } catch (err) {
      // console.log(err);
    }
  }

  // Получить результат сравнения текущего ввода с текущим словом из шаблона
  isMatched(text) {
    // const SPACE = ' '

    // console.log(
    //   `DEBUG: this.props.currentWord = ${this.props.currentWord}; ${
    //     this.props.game.text.split(SPACE)[this.props.currentWord]
    //   }`
    // );
    // console.log(
    //   `DEBUG: ${this.props.game.text.split(SPACE)[this.props.currentWord] ===
    //     text}`
    // );

    return (
      this.props.game.text.split(SPACE)[this.props.currentWord] === text.trim()
    );
  }

  // метод-обработчик события изменения содержания контрола ввода данных, е - содержит событие
  // кажется нимного странным, что имя метода совпадает с именем метода в родительской компоненте
  // но родительский метод становится доступным через пропсы под другим именем this.props.onEnterTextChange
  handleEnterTextChange(e) {
    // вызов метода родительской компоненты полученного через пропсы
    // для внесения измения в состояние родительской компоненты

    // this.props.onEnterTextChange(e.target.value)
    // console.log(e.target.value)

    // if (typeof e.target.value === 'string') {
    //   console.log(`Да, имеем дело со строкой`)
    // }

    // Запустить таймер, сохранить момент начала игры
    if (!this.props.isTimerStart) {
      this.props.onTimerStart();
    }

    // анализ и обработка строки ввода после каждого нового введенного символа
    let str = e.target.value;
    // DEBUG добыть последний символ в строке
    // console.log(`${str} | ${str[str.length - 1]}`);

    // проверка на совпадение ввода с текущим словом, если не совпадает то завершение работы обработчика
    if (!this.isMatched(str)) {
      this.props.onEnterTextChange(e.target.value);
      return;
    }

    // проверка на завершение слова пробелом
    if (str[str.length - 1] === " ") {
      str = "";
      // увеличить значение курсора в тексте на один
      // или завершить игру если достигнут конец текста
      if (
        this.props.game.text.split(SPACE).length ===
        this.props.currentWord + 1
      ) {
        // обработчик родительского компонента для перевода в состояние "игра завершена"
        this.props.onGameOver();
        return;
      }
      this.props.increaseCurrentWord();
    }
    this.props.onEnterTextChange(str);
  }

  // Условный рендеринг в зависимости от текущего состояния игры (первая, начата, завершена)
  render() {
    if (this.props.isGameOver) {
      return (
        <div>
          <p>Game Over! Click the button New Game to start of the game</p>
        </div>
      );
    }

    if (this.props.isGameStart) {
      return (
        <form>
          <input
            type="text"
            value={this.props.enterText}
            onChange={this.handleEnterTextChange}
            ref={this.textInput}
            onKeyPress={e => {
              if (e.key === "Enter") e.preventDefault();
            }}
          />
        </form>
      );
    }

    return (
      <div>
        <p>Click the button New Game to start</p>
      </div>
    );
  }
}

//  Пропсы полученныие из состояния компоненты родителя
// enterText = { this.state.enterText }     // иметь возможность сравнивать введенный текст с текущим словом
// currentWord = { this.state.currentWord }
class Pattern extends Component {
  render() {
    // const SPACE = ' '
    let Selected = {
      color: "blue",
      textDecoration: "underline"
    };

    //  из this.props.game.text собрать новую строку с выделенным словом по this.props.currentWord
    // получить массив слов, разделитель пробел
    let arr = this.props.game.text.split(SPACE);
    // arr[this.props.currentWord] = '[' + arr[this.props.currentWord] + ']'

    // Показать ошибку если ввод не совпадает с началом текущего слова
    if (arr[this.props.currentWord].indexOf(this.props.enterText) !== 0) {
      Selected.color = "red";
    }

    if (!this.props.isGameStart) {
      return (
        <div>
          <p>Text is hidden until the start of the game</p>
        </div>
      );
    }

    if (this.props.isGameOver) {
      return (
        <div>
          <p>{this.props.game.text}</p>
        </div>
      );
    }

    return (
      <div>
        <p>
          {arr.slice(0, this.props.currentWord).join(SPACE) + SPACE}
          <span style={Selected}>{arr[this.props.currentWord]}</span>
          {SPACE}
          {arr.slice(this.props.currentWord + 1).join(SPACE)}
        </p>
      </div>
    );
  }
}

