class Results extends Component {
  // время (с десятыми), скорость(зн/мин), количество ошибок, процент ошибок

  render() {
    function formatms2time(a) {
      const ms = Math.floor(a / 100) % 10;
      const sec = Math.floor(a / 1000) % 60;
      const min = Math.floor(a / 60000) % 60;
      return (
        min.toString().padStart(2, "0") +
        ":" +
        sec.toString().padStart(2, "0") +
        "." +
        ms
      );
    }

    return (
      <div>
        <h4>Results</h4>
        <ul>
          {this.props.results.map(res => (
            <li key={res.id}>
              {formatms2time(res.time)} {res.speed} CPM
            </li>
          ))}
        </ul>
      </div>
    );
  }
}

// компонент с состоянием, которое будет содержать введенное слово, и курсор в шаблоне. Пробел завершает ввод слова
// после чего слово надо удалить из ввода и передвинуть курсор на следующее
class GameField extends Component {
  // инициализировать начальное состояние ввода и курсора в шаблоне
  constructor(props) {
    super(props);
    this.state = {
      seconds: 0,
      enterText: "",
      currentWord: 0,
      isGameStart: false,
      isGameOver: false,
      isTimerStart: false,
      timeInterval: { from: 0, to: 0 },
      results: [
        // время (с десятыми), скорость(зн/мин), количество ошибок, процент ошибок
        // {id: 42, time: 100, speed: 314},
      ]
    };

    // магия, без которой не получается вызвать метод из дочерней компоненты
    this.handleEnterTextChange = this.handleEnterTextChange.bind(this);
    this.increaseCurrentWord = this.increaseCurrentWord.bind(this);
    this.handleNewGameStart = this.handleNewGameStart.bind(this);
    this.handleGameOver = this.handleGameOver.bind(this);
    this.handleTimerStart = this.handleTimerStart.bind(this);
  }

  // метод обработчик изменения состояния, на вход получает новое значение и обновляет им
  // свое состояние, интересно, что вызывается из дочерней компоненты
  handleEnterTextChange(enterText) {
    // вызов специального метода для изменения состояния, прямое присваивание нового значения не работает
    this.setState({
      enterText: enterText
    });
  }

  // передвинуть курсор в массиве слов на следующее слово
  increaseCurrentWord() {
    this.setState({
      currentWord: this.state.currentWord + 1
    });
  }

  // обработчик изменения состояния для реализации клика на новую игру
  handleNewGameStart() {
    this.setState({
      isGameStart: true,
      isTimerStart: false,
      seconds: 0,
      isGameOver: false,
      currentWord: 0,
      enterText: ""
    });
    // остановить работу функции setInterval
    clearInterval(this.interval);
  }

  tick() {
    this.setState(state => ({
      seconds: state.seconds + 1
    }));
  }

  // обработчик изменения состояния для реализации включения таймера по первому введенному символу
  handleTimerStart() {
    this.setState({
      isTimerStart: true,
      seconds: 0,
      timeInterval: { from: Date.now(), to: 0 }
    });
    this.interval = setInterval(() => this.tick(), 1000);
  }

  // обработчик изменения состояния для реализации завершения игры
  handleGameOver() {
    const stopMoment = Date.now();

    const newResult = {
      id: stopMoment,
      time: stopMoment - this.state.timeInterval.from,
      speed: Math.floor(
        (this.props.game.text.length /
          ((stopMoment - this.state.timeInterval.from) / 1000)) *
          60
      )
    };
    this.setState(state => ({
      isGameOver: true,
      isTimerStart: false,
      timeInterval: { from: state.timeInterval.from, to: stopMoment },
      results: state.results.concat(newResult)
    }));
    clearInterval(this.interval);

    // сохранить результы игры в текущие результаты приложения
    this.props.onResultsChange(newResult);
  }

  // не понятна полезность этого кода
  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    return (
      <div>
        <Pattern
          // передать в дочернюю компоненту данные с игрой
          game={this.props.game}
          // передать в дочернюю компоненту собственное состояние
          enterText={this.state.enterText} // иметь возможность сравнивать введенный текст с текущим словом
          currentWord={this.state.currentWord}
          isGameStart={this.state.isGameStart}
          isGameOver={this.state.isGameOver}
        />
        <Input
          // передать в дочернюю компоненту данные с игрой
          game={this.props.game}
          // передать в дочернюю компоненту собственное состояние
          seconds={this.state.seconds}
          isTimerStart={this.state.isTimerStart}
          enterText={this.state.enterText}
          currentWord={this.state.currentWord}
          isGameStart={this.state.isGameStart}
          isGameOver={this.state.isGameOver}
          // передать в дочернюю компоненту собственный метод для изменения собственного состояния
          onEnterTextChange={this.handleEnterTextChange}
          increaseCurrentWord={this.increaseCurrentWord}
          onGameOver={this.handleGameOver}
          onTimerStart={this.handleTimerStart}
        />
        <br />
        {/* <p>&gt;{this.state.enterText}&lt;</p> */}
        <NewGame
          // Передать в кнопку состояние флага "Игра начата"
          isGameStart={this.state.isGameStart}
          // Передать в дочернюю компоненту пропсами метод для изменения собственного состояния в "isGameStart = true"
          onNewGameStart={this.handleNewGameStart}
        />
        <Timer
          seconds={this.state.seconds}
          isTimerStart={this.state.isTimerStart}
        />
        <Results results={this.state.results} />
      </div>
    );
  }
}

function getText(game) {
  // console.log(game)
  let g = game;

  // Если словарь это массив слов то выбрать из него 60 случайных и построить из них текстовую строку
  if (game.dicType === "words") {
    g.text = getNRandomWords(g.words).join(" ");
  }
  return g;
}

const TypeFast = function(props) {
  return (
    <div>
      <GameField
        game={getText(props.game)}
        onResultsChange={props.onResultsChange}
      />
    </div>
  );
};

export default TypeFast;
